code,
#code,
.CodeMirror {
  font-family: 'source code pro', 'ubuntu Mono', monospace;
  line-height: 1.4;
}

#code,
body .CodeMirror {
  min-height: 200px;
  border: 1px solid #999;
  border-radius: 1px;
  height: auto;
  max-width: 100%;
  width: 100%;
  padding: 8px;
  white-space: pre-wrap;
}

.CodeMirror-scroll {
  max-height: 60vh;
}

.cm-s-default .cm-keyword {
  color: blue;
}

.cm-s-default .cm-pragma {
  color: #b30197;
}

.cm-s-default .cm-comment {
  color: green;
  font-style: italic;
}

.cm-s-default .cm-string {
  color: crimson;
}

.cm-s-default .cm-operator {
  color: #222;
}

.cm-s-default .cm-variable,
.cm-s-default .cm-punctuation,
.cm-s-default .cm-property {
  color: red;
}

.cm-s-default .cm-variable-3 {
  color: grey;
}

.cm-s-default .cm-number {
  color: #90368a;
}

.cm-s-default .cm-basic-line-number {
  color: grey;
  text-decoration: none;
  display: inline-block;
  width: 4ch;
  text-align: right;
}

.cm-goto {
  text-decoration: underline;
}

.goto .cm-goto {
  cursor: pointer;
}
